import "./Home.css"
import {Link} from "react-router-dom";
import {useState} from "react";

function Login(){
    const [name, setName] = useState("");
    const [password, setPassword] = useState("");

    function onChangeName(event){
        setName(event.target.value);
    }

    function onChangePassword(event){
        setPassword(event.target.value);
    }

    const onSubmit = async () => {
        const json = await (
              await fetch(
                `/mono/login?username=${name}&password=${password}`
              )
            ).json();
        MoveHome(json);
    }

    const MoveHome = (json) => {
        alert('Login Result: '+json);
    }

    return (
        <div className="form-signin">
            <h1 className="h3 mb-3 fw-normal">
                Kukie
            </h1>
                <div className="form-floating">
                    <input
                        type="id"
                        className="form-control"
                        id="floatingIdInput"
                        placeholder="ID"
                        name="username"
                        value={name}
                        onChange={onChangeName}
                    />
                    <label htmlFor="floatingInput">
                        ID
                    </label>
                </div>
                <div className="form-floating">
                    <input
                        type="password"
                        className="form-control"
                        id="floatingPasswordInput"
                        placeholder="Password"
                        name="password"
                        value={password}
                        onChange={onChangePassword}
                    />
                    <label htmlFor="floatingInput">
                        Password
                    </label>
                </div>
                <button onClick={onSubmit} className="w-100 btn btn-lg btn-primary" >Sign in</button>

            <hr/>
            <p><Link to={`/register`}>Register</Link></p>
        </div>
    );

}

function Register(){
    const [name, setName] = useState("");
    const [password, setPassword] = useState("");

    function onChangeName(event){
        setName(event.target.value);
    }

    function onChangePassword(event){
        setPassword(event.target.value);
    }

    const onSubmit = async () => {
        const json = await (
              await fetch(
                `/mono/registerUser?username=${name}&password=${password}`
              )
            ).json();
        MoveLogin(json);
    }

    const MoveLogin = (json) => {
        alert('Register Result: '+json);
    }

    return (
        <div className="form-signin">
            <h1 className="h3 mb-3 fw-normal">Register</h1>

            <p className="textP">Name</p>
            <input
                type="text"
                id="registName"
                className="form-control"
                name="Name"
                value={name}
                onChange={onChangeName}
            />
            <p className="textP">Password</p>
            <input
                type="password"
                id="registPassword"
                className="form-control"
                name="password"
                value={password}
                onChange={onChangePassword}
            />
            <br/><br/>
            <button onClick={onSubmit} className="w-100 btn btn-lg btn-primary">Sign Up</button>

            <hr/>

        </div>
    )
}


export {
    Login,
    Register,
};