import {BrowserRouter, Route, Routes, Link} from "react-router-dom";
import './App.css';
import {Login, Register} from "./routes/Home";

function App() {
  return (
      <BrowserRouter>
          <Routes>
              <Route exact path="/" element={<Login/> } />
              <Route path="/register" element={<Register />} />
              <Route path="/home" element={<h1>Home</h1> } />
          </Routes>
      </BrowserRouter>
  );
}

export default App;
